import React, { Component } from 'react';
import './App.css';

var sortJsonArray = require('./components/SortJson');
const queryJson = require("./components/QueryJson");

class App extends Component {
  
  state = {
    data: require('./assets/data.json'),
    current_sort: {
      "key": "id",
      "order": "asc"
    }
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Bitstudents</h1>
        </header>
        <div className="container">
          <li>
            <label>Search</label>
            <input type="text" onChange={this._searchJson.bind(this)} />
            <span>Search for, Id, Name, Email or Ip</span>
        </li>
          <div id="users">
            <table>
              <tbody>
              <tr>
                <th onClick={ () => this._sortData('id') }>Id</th>
                <th onClick={ () => this._sortData('first_name') }>Firstname</th>
                <th onClick={ () => this._sortData('last_name') }>Lastname</th>
                <th onClick={ () => this._sortData('email') }>Email</th>
                <th onClick={ () => this._sortData('gender') }>Gender</th>
                <th onClick={ () => this._sortData('ip_address') }>Ip adress</th>
              </tr>
            {
              this.state.data.map((row, i) => (       
                <tr key={i}>
                  <td>{row.id}</td>
                  <td>{row.first_name}</td>
                  <td>{row.last_name}</td>
                  <td>{row.email}</td>
                  <td>{row.gender}</td>
                  <td>{row.ip_address}</td>
                </tr>
              ))             
            }
            </tbody>
            </table>
          </div>
        </div>  
      </div>
    );
  }

  _searchJson(event) {
    let d = require('./assets/data.json');        
    let value = event.target.value;
    if (value === "") {
      this.setState({data: d});
      return;
    }
    
    const regex = new RegExp(value, 'i');    
    const result = queryJson.search(d, regex);

    let to_return = [];
    let ids = [];
    for (var i = 0; i < result.length; i++) {
      var pos = result[i][0];
        let found = ids.indexOf(pos) > -1;
        if (!found) {
          ids.push(pos);
        }
    }

    for (var i = 0; i < ids.length; i++) {
      to_return.push(d[ids[i]]);      
    }
    this.setState({data: to_return});
    
  }


  _sortData(key) {
    let d = require('./assets/data.json');    

    if (this.state.current_sort.key === key) {

      if (this.state.current_sort.order === 'asc') {
        var sort = 'des';
      } else {
        var sort = 'asc';
      }
      this.setState({current_sort: {
        "key": key,
        "order": sort
      }});

      if (key === 'id') {
        if (sort === 'asc') {
          var SortedData = sortJsonArray(d, key, sort, 'num');           
        } else {
          var SortedData = d.reverse();                              
        }
      } else {
        var SortedData = sortJsonArray(d, key, sort);        
      }

    } else {
      this.setState({current_sort: {
        "key": key,
        "order": "asc"
      }});

      if (key === 'id') {
        if (sort === 'asc') {
          var SortedData = sortJsonArray(d, key, sort, 'num');
          var SortedData = d.reverse();                                        
        } else {
          var SortedData = sortJsonArray(d, key, sort, 'num'); 
          //var SortedData = d.reverse();                              
        }
      } else {
        var SortedData = sortJsonArray(d, key, 'asc');
      }
    }
  } 
  
}

export default App;



